/**
 * 一款免费的开源性能监控SDK
 *
 * @remarks
 * 目前能够完成监控的指标包含FCP等
 *
 * @packageDocumentation
 */
import { config } from './config';
import { D, W, WN, WP } from './data/constants';
import { initPerformanceObserver } from './performance/observe';
import { isPerformanceSupported } from './tools/isSupported';
import { IYidengOptions } from './typings/types';

export default class Yideng {
  private v = '1.0.0';
  constructor(options: IYidengOptions = {}) {
    // 扩展基础配置
    config.analyticsTracker = options.analyticsTracker;
    config.isResourceTiming = !!options.resourceTiming;
    config.isElementTiming = !!options.elementTiming;
    config.maxTime = options.maxMeasureTime || config.maxTime;
    if (!isPerformanceSupported()) {
      return;
    }
    if ('PerformanceObserver' in W) {
      initPerformanceObserver();
    }
    // console.log('[ options ]', options);
  }
}
